$path: "../../../node_modules/govuk_frontend_toolkit/images/";
@import "govuk-elements";
@import "./print.scss";

/**************************
* Gobal Overides
***************************/
table td {
  vertical-align: top;
}

table.c-table-vam td {
  vertical-align: middle
}

/**************************
* Govuk stuff
***************************/
.govuk-related-items {
    margin-top: $gutter;
    border-top: 8px solid $govuk-blue;
    padding-top: 5px;
    .heading-medium {
        margin-top: 0.3em;
        margin-bottom: 0.5em;
    }
    li {
        margin-bottom: 10px;
        list-style-type: none;
    }
}

.change-answer {
  text-align: right;
}


/**************************
* Objects
***************************/

.o-question {
    padding-top: $gutter;
}


/**************************
* Components
***************************/

.c-header-proposition__name {
    font-family: "nta", Arial, sans-serif;
    font-weight: 400;
    text-transform: none;
    font-size: 24px;
    line-height: 1.25;
    font-weight: bold;
    color: white !important;
    text-decoration: none;
}

.c-global-header__wrapper {
    color: #FFF;
    font-weight: bold;
    margin-bottom: 20px;
}

.c-global-header__username {
    vertical-align: middle;
}

.c-profile-logout {
    display: inline-block;
    background: none;
    color: #FFF;
    border: none;
    font-size: 16px;
    font-weight: bold;
    padding: 0.5em;
    cursor: pointer;
}

.c-main-heading-link {
  display: block;
  cursor: pointer;

  @media screen and (min-width: 641px) {
    text-align: right;
    margin-top: 2.8em;
  }
}

.c-dashboard-header {
    border-bottom: solid 1px $grey-2;
    padding-top: $gutter;
    padding-bottom: $gutter;
}

.c-offender-count {
    display: block;
    font-size: 24px;
    line-height: 1.25;
    font-weight: bold;
    padding-top: 8px;
}

.c-profile-holder {
    width: 50px;
    height: 50px;
    display: block;
    background-image: url("/assets/images/profile-placeholder.gif");
    background-size: contain;
    background-repeat: no-repeat;
}

.c-date-title {
    margin-bottom: 3em;
}

.c-risk-threat {
    text-transform: uppercase;
}

.c-risk-threat--low {
    color: $turquoise;
}

.c-risk-threat--medium {
    color: $orange;
}

.c-risk-threat--high {
    color: $red;
}

.c-risk-threat--unknown {
    color: $mauve;
}

.c-form-label-container {
    margin-top: $gutter;
    margin-bottom: $gutter;
}

.c-message-text {
    font-weight: bold;
    margin-top: 15px;
}

.c-messgae-text--success {
    color: $turquoise;
}

.c-btn-right--xcustom {
    margin-top: 100px;
    float: right;
}

.c-table-start-btn {
    font-size: 20px;
}

.c-status-indicator {
    padding: 0.3em 0.8em;
    border: solid 1px $grey-2;
    display: inline-block;
    background-color: $grey-3;
    text-transform: uppercase;
    font-size: 1rem;
    font-weight: bold;
    text-align: left;
}

.c-offender-details-container {
    width: 100%;
    margin-top: $gutter;
    padding-top: $gutter;
    padding-bottom: $gutter;
    margin-bottom: 70px;
    border-top: 1px solid $grey-2;
    border-bottom: 1px solid $grey-2;
}

.c-offender-profile-image {
    float: left;
    max-width: 150px;
    height: auto;
    margin-right: 40px;
}

.c-profile-holder--global-header {
    float: none;
    display: inline-block;
    vertical-align: middle;
    width: 35px;
    height: 35px;
    margin-right: 0;
    margin-left: 10px;
}

.c-offender-image {
  float: left;
  max-width: 180px;
  padding-right: 1em;
}

.c-offender-image img {
  max-width: 100%;
  height: auto;
}

.c-offender-details {
  float: left;
}

.c-offender-details td,
.c-offender-details th {
  border: none;
}

.c-offender-profile-image img {
    max-width: 100%;
    height: auto;
}

.c-offender-profile-details {
    float: left;
    padding-top: 15px;
}

.c-offender-profile-item {
    display: block;
    text-transform: capitalize;
}

.c-row-title {
    font-size: 24px;
}

.c-print-link {
    text-align: left;
    margin: 2em 0 0 0;
    padding: 1em;
}

.c-print-link--small-mt {
  margin-top: 0;
  text-align: left;

  @media screen and (min-width: 641px) {
    text-align: right;
    margin-top: 1em;
  }
}

.c-icon-button {
    cursor: pointer;
    background-color: transparent;
    border: none;
    display: inline-block;
    background-image: url('/assets/images/icon-print.png');
    background-position: 0% 50%;
    background-repeat: no-repeat;
    background-size: 16px 18px;
    padding: .5em 0 .5em 28px;
    zoom: 1;
    font-size: 1.8rem;
}

.c-text-hint {
  color: $secondary-text-colour;
}


.c-radio-comment {
  width: 100%;
  margin-bottom: 10px;
}

.c-radio-comment__label {
  display: block;
  margin-bottom: 10px;
}

.c-radio-comment__comment-box {
  width: 100%;
  padding-bottom: 1.5em;
}

.c-answers-table-vat {
  vertical-align: top;
}

.c-answers-table-vat td {
  width: 50%;
}

.c-answers-table-vat p:first-child {
  margin-top: 0;
}


.c-answers-table-vat p:last-child {
  margin-bottom: 0;
}
.c-notice {
  margin-bottom: 2em;
}

.c-reasons-list {
  margin-bottom: 2em;
}

.c-reasons-list li {
  margin-bottom: 1em;
}


/*******************
* Utilities
*******************/

.u-clear-fix {
    content: "";
    display: table;
    clear: both;
}

.u-no-borders {
  border: none;
}

.u-text-align-center {
    text-align: center;
}

.u-text-align-right {
    text-align: right;
}

.u-margin-top-default {
    margin-top: $gutter;
}

.u-margin-top-charlie {
  margin-top: 2em;
}

.u-margin-bottom-default {
    margin-bottom: $gutter;
}

.u-margin-bottom-alpha {
  margin-bottom: 6em;
}

.u-margin-bottom-bravo {
    margin-bottom: 3em;
}

.u-margin-bottom-charlie {
    margin-bottom: 2em;
}

.u-margin-bottom-small {
    margin-bottom: 1em;
}

.u-no-margin-top {
    margin-top: 0;
}

.u-no-margin-bottom {
    margin-bottom: 0;
}

.u-no-margin {
    margin: 0;
}

.u-d-block {
    display: block;
}

.u-link {
  cursor: pointer;
  border: none;
  background: none;
  font-size: inherit;
}

.u-no-link {
    text-decoration: none;
    color: $text-colour !important;
}

.u-text-capitalize {
    text-transform: capitalize;
}

.u-font-weight-normal {
  font-weight: normal !important;
}


.u-spacing-right {
  @media screen and (min-width: 641px) {
    display: inline-block;
    margin-left: 1em;
  }
}

.u-print-hide {
  @media print {
    display: none;
  }
}

@media screen and (min-width: 641px) {
    .c-header-global {
        width: 20% !important;
    }
    .c-header-proposition {
        width: 80% !important;
    }
    .c-global-header__wrapper {
        text-align: right;
        float: right;
        margin-top: -5px;
        margin-bottom: 0;
    }

    .govuk-related-items {
      margin-top: 0;
    }

    .c-prisoner-name {
      margin-top: 60px;
    }
}




